.mask{-webkit-mask-size:100% 100%;mask-size:100% 100%;-webkit-mask-image:radial-gradient(var(--mask-radius) at var(--mask-position-x) var(--mask-position-y), black 0%, transparent 100%);mask-image:radial-gradient(var(--mask-radius) at var(--mask-position-x) var(--mask-position-y), black 0%, transparent 100%)}.gradient{background:radial-gradient(ellipse var(--gradient-width) var(--gradient-height) at var(--gradient-position-x) var(--gradient-position-y), var(--gradient-color-start), var(--gradient-color-end));width:400%;height:400%;left:-150%;top:-150%;transform:rotate(var(--gradient-tilt));transform-origin:center}.lines{background-size:var(--lines-size) var(--lines-size);background-position:center}.dots{background-image:radial-gradient(var(--dots-color) 1px, var(--static-transparent) 1px);background-size:var(--dots-size) var(--dots-size)}