.chip{white-space:nowrap;-webkit-user-select:none;-moz-user-select:none;user-select:none}.chip.selected{background:var(--brand-alpha-medium);color:var(--brand-on-background-medium)}.chip.selected:hover,.chip.selected:focus{background:var(--brand-alpha-medium)}.chip.selected:active{background:var(--brand-alpha-weak);color:var(--brand-on-background-weak)}.chip.unselected{background:var(--neutral-alpha-weak);color:var(--neutral-on-background-medium)}.chip.unselected:hover,.chip.unselected:focus{background:var(--neutral-alpha-medium)}.chip.unselected:active{background:var(--neutral-alpha-weak);color:var(--neutral-on-background-weak)}.chip:disabled{background:var(--neutral-solid-weak);cursor:not-allowed}