.line{background-color:var(--neutral-on-background-strong);height:1px;width:var(--static-space-24);transition:transform .3s ease;position:absolute;left:50%;top:50%}.line:first-child{transform:translateX(-50%) translateY(calc(-1 * var(--static-space-4)))}.line:last-child{transform:translateX(-50%) translateY(var(--static-space-4))}.active:first-child{transform:translateX(-50%) translateY(0) rotate(45deg)}.active:last-child{transform:translateX(-50%) translateY(0) rotate(-45deg)}